@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-Light.woff") format("woff"), url("../fonts/OpenSans-Light.woff") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-Regular.woff") format("woff"), url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-SemiBold.woff") format("woff"), url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-Bold.woff") format("woff"), url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.38;
  font-family: "Open-Sans", "Helvetica", sans-serif; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea,
select,
button {
  font: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.page-header {
  background-color: #000; }

.page-header__wrapper {
  height: 825px;
  background: #000 url("../img/promo-bike.jpg") no-repeat;
  background-size: contain;
  background-position: center top;
  position: relative; }

.page-header__grid {
  max-width: 879px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 62px 255px 267px;
  margin: 0 auto; }

.page-header__title,
.page-header__feedback {
  margin-top: 7px; }

.main-nav {
  grid-row: 1 / 2;
  grid-column: 1 / 4;
  padding: 0 2.75px; }

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding-top: 14px; }

.main-nav__item {
  padding: 13px 20px; }

.main-nav__link {
  font-weight: 300;
  color: #fff;
  opacity: 0.7; }

.logo {
  grid-row: 3 / 4;
  grid-column: 1 / 2; }

.title {
  max-width: 350px;
  grid-row: 3 / 4;
  grid-column: 1 / 3; }

.title__title {
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  color: #fff;
  letter-spacing: -0.005em;
  margin-top: 46px;
  margin-bottom: 25px; }

.title__text {
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  color: #fff;
  letter-spacing: -0.005em;
  max-width: 380px; }

.feedback {
  max-width: 327px;
  grid-row: 3 / 4;
  grid-column: 3 / 4; }

.feedback__fieldset {
  border: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column; }

.feedback__legend {
  color: #fff;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em;
  padding: 0;
  margin-bottom: 18px; }

.feedback__input {
  width: 327px;
  height: 48px;
  border: 1px solid #c9ccd4;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 14px 29px 10px 29px; }
  .feedback__input::-moz-placeholder {
    font-size: 15px;
    line-height: 24px;
    color: #1c1c1c;
    opacity: 0.14; }
  .feedback__input::placeholder {
    font-size: 15px;
    line-height: 24px;
    color: #1c1c1c;
    opacity: 0.14; }

.feedback__btn {
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000;
  background-color: #0ad9c6;
  padding: 16px 51px 15px 53px;
  border-radius: 6px;
  border: none;
  cursor: pointer; }
